import { Component, h, Host, Prop } from '@stencil/core';

/**
 * @category Navigation
 * @description A list selector item component for the list selector.
 * @internal
 *
 * @slot badge - Placed after the item-title
 * @slot info - Placed after the subtitle
 * @slot - Placed at the end the of the element
 */
@Component({
    tag: 'atui-list-selector-item',
    shadow: false,
})
export class AtuiListSelectorItem {
    /**
     * Id of the list item
     */
    @Prop()
    item_id: string;

    /**
     * Title of the list item.
     */
    @Prop()
    item_title: string;

    /**
     * Optional subtitle of the list item.
     */
    @Prop()
    subtitle?: string;

    /**
     * Optional icon shown to the left of the list item - use mat-icon name.
     */
    @Prop()
    icon?: string;

    /**
     * Optional prefix.
     */
    @Prop()
    item_prefix?: string;

    /**
     * Border below the list item.
     */
    @Prop()
    has_border? = false;

    /**
     * Visual indication of the selected item.
     */
    @Prop()
    is_selected?: boolean;

    render() {
        return (
            <Host
                role="menuitem"
                class={`outline-active-light hover:bg-surface-1 focus:bg-surface-1 flex flex-row items-center rounded outline-0 outline-none hover:cursor-pointer focus:outline-2 ${this.is_selected ? 'bg-surface-1 !text-active-dark' : ''} ${this.has_border ? 'border-b' : ''}`}
            >
                <i
                    class="material-icons text-light mr-8 ml-16 items-center text-sm"
                    data-name="item-icon"
                >
                    {this.icon}
                </i>
                <div class="flex flex-grow flex-col py-8">
                    <div class="text-body flex items-center font-medium whitespace-nowrap">
                        {this.item_prefix && (
                            <span
                                class="text-body text-light mr-[16px] font-normal"
                                data-name="item-prefix"
                            >
                                {this.item_prefix}
                            </span>
                        )}
                        <span
                            class={`text-body flex flex-grow truncate pr-8 font-medium ${this.item_prefix && this.subtitle ? 'flex flex-col' : ''}`}
                        >
                            <span class="flex flex-row">
                                {this.item_title && (
                                    <span data-name="item-title">{`${this.item_title} `}</span>
                                )}
                                <slot name="badge" />
                            </span>

                            {this.item_prefix && this.subtitle && (
                                <span
                                    class={`text-body font-normal ${this.is_selected ? 'text-active-dark' : 'text-med'}`}
                                    data-name="item-subtitle-when-prefix-exists"
                                >
                                    {`${this.subtitle}`}
                                </span>
                            )}
                        </span>
                        <slot name="info" />
                    </div>
                    {this.subtitle && !this.item_prefix && (
                        <span
                            class="text-med inline text-sm font-normal"
                            data-name="item-subtitle"
                        >
                            {this.subtitle}
                        </span>
                    )}
                </div>
                <slot />
            </Host>
        );
    }
}
